


































import { Component, Vue } from "vue-property-decorator";
import ExpressWord from "@/components/ExpressWord.vue";
import ExpressDefinition from "@/components/ExpressDefinition.vue";
import BottomBar from "@/components/BottomBar.vue";
import { Comment } from "@/types/comment";
import api from "@/data/apis";
import { Word } from "@/types/word";
import { SquareGrid } from "vue-loading-spinner";
import { Nullable } from "@/types/nullable";
import console from "@/util/console";

@Component({
  components: {
    ExpressWord,
    ExpressDefinition,
    SquareGrid,
    BottomBar,
  },
})
export default class SpecificWord extends Vue {
  currentWord: Nullable<Word> = null;
  waiting = false;
  hasFinished = false;
  enabledBackButton = false;
  showDefinition = false;

  async mounted(): Promise<void> {
    const word = `${this.$route.query.w}`;
    // this.admobFreeService.prepareAdmobFree();
    // this.admobFreeService.BannerAd();
    // this.ws.reset();
    this.waiting = true;
    const data = await api.get(word);
    if (data.body.length > 0) {
      this.currentWord = data.body[0];
      this.waiting = false;
    } else {
      const comment = new Comment();
      comment.title = word;
      comment.word = word;
      comment.author = "web app user - specific word not found";
      await api.addComment(comment).then((result) => {
        console.log("comments - saved", result);
      });
      this.$router.push("/");
    }
  }

  toogleDefinition(): void {
    console.log("home - toogleDefinition");
    this.showDefinition = !this.showDefinition;
  }
}
